import React, { Component } from 'react';
import { Link } from "react-router-dom";

import ShareButton from '../ShareButton';
// import LikeButton from '../LikeButton';

import './style.css';

export default class Post extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isLiked: false
    };

    this.handleClickIsLiked = this.handleClickIsLiked.bind(this);
  }

  handleClickIsLiked() {
    this.setState((prevState) => {
      return {isLiked: !prevState.isLiked}
    });
  }

  render() {
    const { post } = this.props;
    return (
      <div className="post">
        <Link className="post__body" to={{ pathname: `/modal/${post.id}`, state: { modal: true } }}>
          <img src={post.img} alt={post.title} />
        </Link>
        <div className="post__bottom">
          <div className="post__buttons">
            {/* <LikeButton isLiked={this.state.isLiked}
                        handleClickIsLiked={this.handleClickIsLiked.bind(this)}
            /> */}
            <ShareButton post={post} />
          </div>
          <div className="post__title">
            {post.title}
            <br/>
            <strong>Type: {post.type}</strong>
          </div>
        </div>
      </div>
    );
  }
}